@media(max-width:910px) {
    .transaccion-area-ts {
        display: flex;
        flex-direction: column-reverse;
        gap: 10px;
        height: fit-content;
    }
}

@media(max-width:720px) {
    .contenido-ts .walletOpcion {
        width: 100%;
        height: 100px;
    }
}

@media(max-width:520px) {
    .contenido-ts .wallets {
        flex-direction: column;
        gap: 10px;
    }
}

@media(max-width:520px) {
    .confirmarTransaccion-contenido {
        align-self: baseline;
    }
}
 